<template>
    <div>
        <div class="title">数据分析</div>
        <div class="tips">注意：仅支持已上传的简历</div>
        <div class="box">
            <div class="sex" id="sex"></div>
            <div class="age" id="age"></div>
            <div class="line" id="line"></div>
            <div class="edu" id="edu"></div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'dataAnalyze',
    methods: {
        sexImg() {
            var chartDom = document.getElementById('sex');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: '性别',
                    left: 'center',
                    top: '0'
                },
                tooltip: {
                    trigger: 'item',
                    formatter: '{d}%'
                },
                dataset: [
                    {
                        source: [
                            ['Product', 'Sales', 'Price', 'Year'],
                            ['男', 50, 32, 2011],
                            ['第三性别', 5, 25, 2011],
                            ['未知', 10, 25, 2011],
                            ['女', 25, 25, 2011]
                        ]
                    },
                    {
                        transform: {
                            type: 'filter',
                            config: { dimension: 'Year', value: 2011 }
                        }
                    },
                ],
                series: [
                    {
                        type: 'pie',
                        radius: 150,
                        center: ['100%', '100%'],
                        datasetIndex: 1
                    },
                ],
                // Optional. Only for responsive layout:
                media: [
                    {
                        query: { minAspectRatio: 1 },
                        option: {
                            series: [
                                { center: ['50%', '50%'] },
                            ]
                        }
                    },
                ]
            };

            option && myChart.setOption(option);
        },
        ageImg() {
            var chartDom = document.getElementById('age');
            var myChart = this.$echarts.init(chartDom);
            var option;
            myChart.resize({
                width: 600,
                height: 500
            })
            option = {
                title: {
                    text: '年龄',
                    left: 'center',
                    top: '0'
                },
                xAxis: {
                    type: 'category',
                    data: ['18-23', '23-28', '28-33', '33-38', '38-43', '48+']
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                    {
                        data: [90, 120, 150, 80, 70, 5],
                        type: 'bar'
                    }
                ]
            };

            option && myChart.setOption(option);

        },
        eduImg() {
            var chartDom = document.getElementById('edu');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: '学历',
                    left: 'center',
                    top: '0'
                },
                tooltip: {
                    trigger: 'item',
                    formatter: ' {c} ({d}%)'
                },
                series: [
                    {
                        name: 'Area Mode',
                        type: 'pie',
                        radius: [40, 200],
                        center: ['50%', '50%'],
                        roseType: 'area',
                        itemStyle: {
                            borderRadius: 5
                        },
                        data: [
                            { value: 30, name: '大专' },
                            { value: 10, name: '博士' },
                            { value: 25, name: '硕士' },
                            { value: 10, name: '博士后' },
                            { value: 40, name: '本科' },
                            { value: 15, name: '大专以下' },
                        ]
                    }
                ]
            };

            option && myChart.setOption(option);

        },
        lineImg() {

            var chartDom = document.getElementById('line');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: '上传与收藏数量',
                    left:'center',
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {
                    data: [ '已上传简历', '已收藏简历'],
                    top:'30px'
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: ['7.10', '7.11', '7.12', '7.13', '7.14', '7.15', '7.16','7.17']
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                    {
                        name: '已上传简历',
                        type: 'line',
                        stack: 'Total',
                        data: [50, 60, 43, 80, 20, 54, 90]
                    },
                    {
                        name: '已收藏简历',
                        type: 'line',
                        stack: 'Total',
                        data: [10, 12, 21, 20, 5, 16, 40]
                    }
                ]
            };

            option && myChart.setOption(option);

        }
    },
    mounted() {
        this.sexImg(),
            this.ageImg(),
            this.eduImg(),
            this.lineImg()
    }
}
</script>

<style lang="scss" scoped>
.title {
    width: 200px;
    height: 80px;
    line-height: 80px;
    text-align: center;
    margin: 0 auto;
    font-size: 30px;
    font-weight: 700;
}

.tips {
    margin: 0 auto;
    width: 200px;
    text-align: center;
    transform: translateY(-10px);
}

.box {
    height: 1200px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 100px;
    margin-bottom: 100px;
    .sex {
        height: 600px;
        width: 50%;
    }

    .age {
        height: 600px;
        width: 50%;
    }

    .edu {
        height: 600px;
        width: 50%;
    }

    .line {
        height: 600px;
        width: 50%;
    }
}
</style>